



















import uniLoadMore from "@/components/uni-load-more.vue"
export default {
	data() {
		return {
			btnShow: false,
			list: [],
			maxtime: '',
			page: 1,
			lineShow: false,
			loadingType: 0, //旧
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "- 我是有底线的 -"
			}
		}
	},
	components: {
		uniLoadMore
	},
	onLoad() {
		this.getList();
		this.getInfo()
	},
	onPullDownRefresh() {
		// 下拉刷新
		this.page = 1;
		this.loadingType = 0;
		this.getList()
	},
	onReachBottom() {
		// 上拉加载
		this.getReload();
	},
	methods: {
		getInfo() {
			var that = this;
			// 获取用户信息
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					that.btnShow=false;
				},
				fail: function(ret) {
					that.btnShow=true;
				}
			});
		},
		getList() {
			var that = this;
			uni.request({
				url: 'https://api.budejie.com/api/api_open.php',
				data: {
					a: 'newlist',
					c: 'data',
					page: 1,
					count: 10,
					// 上一页的maxtime作为加载下一页的条件，
					// maxtime: that.maxtime,
					type: '10',
				},
				success: (res) => {
					uni.stopPullDownRefresh(); // 停止刷新
					that.maxtime = res.data.info.maxtime;
					that.list = res.data.list;
				}
			});
		},
		// 上拉加载
		getReload() {
			var that = this;
			if (that.loadingType !== 0) {
				return;
			}
			that.loadingType = 1;
			uni.request({
				url: 'https://api.budejie.com/api/api_open.php',
				data: {
					a: 'list',
					c: 'data',
					// 上一页的maxtime作为加载下一页的条件，
					maxtime: that.maxtime,
					type: '10',
					page: that.page + 1,
				},
				success: (res) => {
					let moreList = res.data.list;
					that.maxtime = res.data.info.maxtime;
					if (moreList.length == 0) {
						that.lineShow = true;
						that.loadingType = 2;
					} else {
						that.page++;
						for (var i = 0; i < moreList.length; i++) {
							that.list.push(moreList[i]);
						}
						that.loadingType = 0;
					}
				}
			});
		},
		// 预览图片(类型必须是数组)
		previewImage(img) {
			var that = this;
			var imgList = [];
			imgList.push(img);
			uni.previewImage({
				urls: imgList
			});
		},
		openInfo() {
			var that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					that.btnShow=false;
				}
			});
		}
	},
	onShareAppMessage(res) {
		return {
			title: '我是媛媛呀',
			path: '/pages/index/index'
		}
	}
}
